@media only screen and (max-width: 1140px){
.logo {
  margin-bottom: 45px;
  max-width: 200px;
}
  .grid {
    padding-top: 60px;
    padding-right: 40px;
    padding-left: 40px;
}
  .illustration{
    padding-right: 20px;
  }
  .register {
    padding-left: 20px;
    h1{
      margin-top: 25px;
      font-size: 2em;
    }
    p {
      font-size: 1em;
    }
    .button{
      font-size: 1em;
      max-width: 180px;
      padding: 10px 40px;
    }
  }
}

@media only screen and (max-width: 960px){
  .grid {
    background: url('../../images/bg-mobile.svg') no-repeat;
    background-size: contain;
    max-width: 600px;
    grid-template: 
      "logo" max-content
      "illustration" max-content
      "register" max-content
      "social" max-content
    / 1fr;
  }

.logo {
  margin-bottom: 45px;
  max-width: 180px;
}
  .grid {
    padding-top: 50px;
    padding-right: 30px;
    padding-left: 30px;
}
  .illustration{
    padding-right: 0px;
    align-items: center;
    max-width: 600px -60px;
    justify-self: center;
  }
  .register {
    padding-left: 0;
    align-items: center;
    h1{
      margin-top: 100px;
      font-size: 2em;
      text-align: center;
    }
    p {
      max-width: 500px;
      font-size: 1em;
      text-align: center;
    }
    .button{
      max-width: 300px;
      padding: 10px 70px;
      margin-top: 20px;
      margin-bottom: 60px;
    }
  }
  .social-media{
    justify-content: center;
  }
}

@media only screen and (max-width: 600px){
  .grid {
    background: url('../../images/bg-mobile.svg') no-repeat;
    background-size: contain;
  }
  .logo {
    max-width: 150px;
  }
  .register {
    h1{
      margin-top: 80px;
      font-size: 1.5em;
    }
  }
  .social-media{
    a{
      display: block;
      flex: 1;
      max-width: 35px;
    }
}
}




