body {
  font-family: 'Open Sans', sans-serif;
  background: hsl(257, 40%, 49%);
}


.grid {
  background: url('../../images/bg-desktop.svg') no-repeat;
  background-size: contain;
  max-width: 1440px;
  margin: 0 auto;
  padding-top: 60px;
  padding-right: 80px;
  padding-left: 80px;
  display: grid;
  // gap: 50px;
  grid-template: 
    "logo logo" max-content
    "illustration register" max-content
    "social social" max-content
  /minmax(500px, 1fr) 1fr;
}

.logo {
  grid-area: logo;
  margin-bottom: 50px;
  max-width: 220px;
}

.illustration {
  grid-area: illustration;
  padding-right: 25px;
  // max-width: 1000px;
}

.register {
  display: flex;
  flex-direction: column;
  grid-area: register;
  // max-width: 550px;
  padding-left: 25px;
  
  h1 {
    margin-top: 40px;
    margin-bottom: 20px;
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    font-size: 2.5em;
    line-height: 1.5;
    color: #fff;
  }
  p {
    margin-bottom: 20px;
    font-size: 1.2em;
    line-height: 1.3;
    font-weight: 400;
    color: #fff;
  }
  .button{
    display: block;
    max-width: 220px;
    padding: 15px 60px;
    border: 2px solid hsl(257, 40%, 49%);
    border-radius: 30px;
    background: #fff;
    color: hsl(257, 40%, 49%);
    cursor: pointer;
    font-size: 1.2em;
    box-shadow: 0 4px 8px 2px rgba(0,0,0,0.2);
  }
}

.social-media{
  max-width: 100%;
  grid-area: social;
  display: flex;
  margin-bottom: 20px;
  margin-top: 20px;
  justify-content: flex-end;
  a{
    display: block;
    flex: 1;
    max-width: 45px;
  }
  a + a {
    margin-left: 12px;
  }
  img {
    display: block;
    border: 2px solid #fff;
    border-radius: 50%;
  }
}

.footer {
  max-width: 1300px;
  margin-top: 20px;
  text-align: center;
  color: #fff;
  a:visited{
    color: #995EC7;
    text-decoration: none;
    font-weight: bold;
  }
  a:link {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
  }
  a:hover {
    transition: .1s;
    text-decoration: underline;
  }
}